body {
    font-family: $font-primary;
    line-height: 2;
    background-color: $color-gray-light;
    width: 100%;
}

.container {
    max-width: 144rem;
    margin: 0 auto;
}

// ------------------------------------------ HEADER
.header {
    position: relative;
    width: 100%;

    &__photo {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 100%;
        height: auto;
        background-size: cover;
        background-repeat: no-repeat;
    }
}

// ------------------------------------------ NAV
.nav {
    padding: 5rem 8rem 0 8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 999;

    &__logo {
        display: block;
    }

    &__btn {
        background-color: $color-gray-light;
        height: 5rem;
        width: 20rem;
        color: $color-atrb;
    }
}

// ------------------------------------------ HERO
.hero {
    @include flex-center(column);
    position: relative;
    z-index: 998;
    margin-top: 13.5rem;
    font-family: $font-secondary;

    &__photo {
        width: 80rem;
        height: auto;
        display: block;
        margin-bottom: 8.5rem;
    }
    
    &__heading {
        width: 80rem;
        text-align: center;
        padding: 0 3rem;
    }

    &__txt {
        font-size: 2.3rem;
        font-weight: 700;
        letter-spacing: 1px;
        word-spacing: .5rem;
    }

    &__description {
        font-size: 1.6rem;
        line-height: 1.8;
        margin-bottom: 2.5rem;
    }

    &__btn {
        @include flex-center(row);
        width: 28rem;
        height: 5.5rem;
        border-radius: 2px;
        margin: 0 auto;
    }
}

// ------------------------------------------ DETAILS
.details {
    padding: 40rem 5rem 0 5rem;
    display: flex;
    position: relative;
    z-index: 997;
    font-family: $font-secondary;

    & > * {
        flex: 1;
        padding: 5rem;
    }

    &__photo {
        width: 100%;
        height: auto;
    }

    &__info-box {
        margin-bottom: 3rem;
    }

    &__heading {
        font-size: 2rem;
        font-weight: 600;
    }

    &__description {
        font-size: 1.3rem;
    }
}

// ------------------------------------------ NAV
.cta {
    padding: 15rem 0;
    @include flex-center(row);
    text-align: center;
    font-family: $font-secondary;

    &__box {
        @include flex-center(column);
        height: 40rem;
        width: 80rem;
        background-color: $color-white;
        box-shadow: 0 1rem 3rem rgba($color-gray, .2);
        border-radius: 5px;
    }

    &__heading {
        font-size: 3rem;
        font-weight: 600;
    }

    &__description {
        font-size: 1.5rem;
        margin-bottom: 5rem;
    }

    &__btn {
        height: 5.5rem;
        width: 28rem;
        border-radius: 3px;
    }
}

// ------------------------------------------ FOOTER
.footer {
    background-color: $color-dark;
    padding: 5rem 8rem;
    display: flex;
    flex-direction: column;
    color: $color-gray-light;


    &__logo {
        fill: $color-white;
        display: inline-block;
        margin-bottom: 3rem;
    }

    &__logo-box {
        display: flex;
    }

    &__icon {
        height: 3rem;
        width: 3rem;
        fill: $color-gray-light;
        display: inline-block;
    }

    &__copyright {
        margin-top: 2.5rem;
        font-size: 1.3rem;
    }

    &__box {
        display: flex;

        & > * {
            flex: 1;
        }
    }

    &__list {
        list-style-type: none;
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        flex: 0 0 50%;
    }

    &__link {
        color: inherit;
        font-size: 1.3rem;
        text-decoration: none;
        margin-bottom: 1.5rem;
        display: inline-block;
    }

    &__subscription {
        display: flex;
        flex-direction: column;
    }

    &__subscribe {
        font-weight: 700;
        text-transform: uppercase;
        font-size: 1.5rem;
    }

    &__description {
        font-size: 1.1rem;
        margin-bottom: 1.5rem;
    }

    input[type=text] {
        border: none;
        width: 100%;
        padding: 1rem;
        border-radius: 5px;
        margin-bottom: 1.5rem;
        font-family: inherit;
    }

    &__btn {
        @include flex-center(row);
        height: 4rem;
        width: 16rem;
        border-radius: 3px;
        align-self: flex-end;
    }
    
    &__frontendmentor {
        text-align: center;
        margin-top: 3rem;

        a {
            text-decoration: none;
            color: $color-primary;
        }
    }
}